import "./Image.css";
import "./Types.css";
const Image = (props) => {
  let fill = props.Type;
  let border;
  if (props.Type2 !== "") border = props.Type2;
  else border = props.Type;
  return (
    <div className={`image ${fill}-light ${border}-border`}>
      <img
        src={`${process.env.PUBLIC_URL}/images/${props.No}.png`}
        alt={props.Name}
      ></img>
    </div>
  );
};

export default Image;
